import React from "react";
import { Link } from "react-router-dom";
import {
  Button,
  Header,
  Icon,
  Segment,
  SegmentInline,
} from "semantic-ui-react";

export default function NotFound() {
  return (
    <Segment placeholder>
      <Header icon>
        <Icon name="search" />
        Oops - We looked everywhere and could not find anything!
      </Header>
      <SegmentInline>
        <Button as={Link} to="/activities" primary>
          Return to Activities page
        </Button>
      </SegmentInline>
    </Segment>
  );
}
